.modal-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0%;
  width: 100%;
  height: 100%;
  z-index: 4;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  cursor: pointer;
}

.confirm-modal {
  position: relative;
  font-size: 1.4rem;
  padding: 1rem;
  padding-bottom: 5rem;
  background-color: white;
  color: var(--text-color--black);
  border-radius: .5rem;
}

.confirm-modal > p {
  margin: 2rem 0 2rem 1rem;
}

.modal-buttons {
  position: absolute;
  right: 0;
}

.modal-buttons > button {
  border: 1px solid var(--icon-color);
  font-weight: 400;
  white-space: nowrap;
  text-align: center;
  cursor: pointer;
  color: #000000d9;
  background-color: white;
  padding: 1rem;
  border-radius: unset;
  transition: all .15s ease-in-out;
}

.cancel-modal-button:hover {
  color: var(--primary-color);
  border-color: var(--primary-color);
}