.edit-modal{
  display: flex;
  flex-direction: column;
  width: 100%;
  position: absolute;
  bottom: 3rem;
  transition: bottom .3s ease-in-out;
  z-index: 3;
}

.top {
  display: flex;
  flex-direction: column;
  width: auto;
  padding-top: 6rem;
  margin: 0 1rem 1rem 1rem;
  justify-content: center;
  align-items: center;
  background: white;
  border-radius: 1rem;
}

img {
  width: 8rem;
}

img + input[type="time"] {
  font-size: 1.6rem;
  padding-left: 2rem;
  color: rgb(136, 136, 136);
  margin-top: 3rem;
  border: none;
  
  background: none;
}

input[type="time"]:hover {
  cursor: pointer;
}

button {
  height: 4rem;
}

button:hover {
  cursor: pointer;
}

.top > button {
  margin-top: 10rem;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  width: 100%;
  border: none;
  border-top: 1px solid var(--icon-color);
  color: var(--primary-color);
  background-color: white;
}

button:nth-child(2) {
  border: none;
  color: var(--danger-color);
  background-color: white;
  flex-grow: 1;
  border-radius: 1rem;
  margin: 0 1rem 3rem 1rem;
}