/* HEADER */
.section__header {
  display: flex;
  justify-content: space-between;
  margin: 2rem 1rem 1rem 1rem;
}

.icon {
  width: 3rem;
  height: 3rem;
}

.section__header > .section__header--left {
  display: flex;
  align-items: center;
  font-size: 2.6rem;
  font-weight: bold;
}

.section__header > * > *:not(:first-child) {
  margin-left: 1rem;
}









/* DATE SELECTOR */
.section__date-selector {
  margin-left: 1.4rem;
  overflow: auto;
  white-space: nowrap;
  scrollbar-width: none;
}

.date-circle {
  position: relative;
  margin-top: 1rem;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 6.5rem;
  height: 6.5rem;
  overflow: hidden;

  border-radius: 50%;
  background-color: var(--primary-color--blur);
  color: var(--text-color--white)
}

.date-circle:not(:first-of-type) {
  margin-left: .7rem;
}

.date-circle span {
  z-index: 2;
}

.date-circle span:nth-child(2) {
  font-size: 1.6rem;
}

.date-circle span:nth-child(3) {
  font-size: 2.6rem;
}

.date-circle[date-status="today"][data-active="true"]{
  border: 2px solid var(--primary-color);
}

.date-circle[date-status="tomorrow"]{
  background-color: white;
  color: var(--primary-color--blur);
  box-shadow: inset 0 0 3px var(--primary-color);
}

.date-circle[date-status="yesterday"][data-active="true"] {
  border: 2px solid var(--secondary-color);
}

.date-circle[date-status="tomorrow"][data-active="true"] {
  border: 2px solid var(--secondary-color);
}

.date-circle:hover {
  cursor: pointer;
}


.circle-progress {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 0%;
  bottom: 0;
  transition: height .3s ease-in-out;
  background-color: var(--primary-color);
}



/* ACTIVATORS */
.section__activator {
  display: grid;
  margin-top: 3rem;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 3rem;
}

.glass-of-water {
  width: 5rem;
  height: 5rem;
}

.water-quantity {
  color: var(--text-color--black);
}

.activator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.activator:hover {
  cursor: pointer;
}

.activator > * {
  margin-bottom: 1rem;
}

.activator__timer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--primary-color);
  padding: 0 0 0 .5rem;
  border-radius: .8rem;
  color: var(--text-color--white);
}

.activator__timer img {
  /* margin-left: .3rem; */
  width: 1.6rem;
  height: 1.6rem;
}






/* FOOTER */

.section__footer {
  position: absolute;
  min-width: 95%;
  height: 8rem;
  bottom: .5rem;
  margin: 0 1rem 0 1rem;

  background: -webkit-linear-gradient(to right, var(--secondary-color), var(--secondary-color--blur));
  background: linear-gradient(to right, var(--secondary-color), var(--secondary-color--blur));

  display: flex;
  flex-direction: row;
  color: var(--text-color--white);
  border-radius: 1rem;
}

.footer--left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 75%;
  margin: 1.4rem;
  border-right: .2rem solid #EFBBAE;
}

.footer--right {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 25%;
  margin: 1rem;
}

.footer__goal {
  font-size: 1.4rem;
  font-weight: bold;
}

.progress-bar {
  width: 90%;
  height: .3rem;
  background: linear-gradient(to right, #EFBBAE, #EFBBAE);
}

.progress {
  height: 100%;
  transition: width .3s ease-in-out;
  background: var(--text-color--white);
}

.footer__left {
  font-size: 1.3rem;
}

.footer--right > img {
  color: var(--text-color--white);
  border-bottom: .2rem solid white;
  width: 2.8rem;
  height: 2.8rem;
}

.footer--right > span {
  margin-top: .3rem;
}



/* OTHER */
.icon:hover {
  cursor: pointer;
}

.overlay {
  display: block;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  background-color: #131313;
  opacity: 0.3;
  z-index: 2;
}

.overlay:hover {
  cursor: pointer;
}