.settings {
  background-color: rgb(245, 245, 245);
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 3;
  font-size: 1.4rem;
  transition: right .3s ease-in-out;
  color: var(--text-color--black);
  align-items: center;
  overflow: scroll;
}

.header {
  font-weight: 600;
  font-size: 1.6rem;
  text-align: center;
  background-color: white;
  padding: 1rem;
  padding-top: 2.6rem;
  /* margin-bottom: 2rem; */
}

.setting_section {
  margin-top: .8rem;
  padding: .5rem 1.8rem .5rem 1.8rem;
  background-color: white;
  border-top: 1px solid #d0cdcd;
  border-bottom: 1px solid #d0cdcd;
}

.setting {
  display: flex;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.setting:not(:last-child) {
  border-bottom: 1px solid #d0cdcd;
}

.setting_value {
  display: flex;
  color: rgb(136, 136, 136);
  align-items: center;
  position: absolute;
  right: 1.8rem;
}

.setting_value:hover {
  cursor: pointer;
}

.setting_value > input {
  color: rgb(136, 136, 136);
  border: none;
  position: absolute;
}

.setting_value > input[type="time"] {
  right: -1.8rem;
}

.setting_value > input[type="number"] {
  text-align: end;
  width: 4rem;
  right: 4.4rem;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.settings > img {
  position: absolute;
  top: 2rem;
  right: 0;
}

.setting_value > img {
  width: 1.8rem;
  height: 1.7rem;
}


.switch {
  position: relative;
  display: inline-block;
  width: 4.5rem;
  height: 2.5rem;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 2rem;
  width: 2rem;
  left: .4rem;
  bottom: .3rem;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(1.8rem);
  -ms-transform: translateX(1.8rem);
  transform: translateX(1.8rem);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

#saveSettings {
  width: 100%;
  text-align: center;
  background-color: #d7d7d7;
  color: var(--text-color--black);
  border: none;
  margin-top: 2rem;
  border-radius: .5rem;
}

#saveSettings:hover {
  cursor: pointer;
  background-color: #acacac;
}